"use client";

import React, { useEffect } from "react";
import { useRouter } from "next/navigation";
import { useAdminStore } from "@/store/admin-store";
import { Card, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Plus, FileText, Calendar, User } from "lucide-react";

const ProcessosPage = () => {
	const router = useRouter();
	const { isAuthenticated, allProcesses, fetchProcesses, isLoading } = useAdminStore();

	useEffect(() => {
		if (!isAuthenticated) {
			router.push("/admin/login");
		} else {
			fetchProcesses();
		}
	}, [isAuthenticated]);

	if (isLoading) {
		return (
			<div className="flex items-center justify-center h-96">
				<p className="text-muted-foreground">Carregando...</p>
			</div>
		);
	}

	return (
		<div className="flex flex-col gap-6">
			<div className="flex items-center justify-between border-b pb-4">
				<div>
					<h1 className="text-2xl font-bold">Processos</h1>
					<p className="text-sm text-muted-foreground">
						Gerencie todos os processos do sistema
					</p>
				</div>
				<Button>
					<Plus className="h-4 w-4" />
					Novo Processo
				</Button>
			</div>

			<div className="grid gap-4">
				{allProcesses.map((process) => (
					<Card key={process.id} className="hover:shadow-md transition-shadow cursor-pointer">
						<CardHeader>
							<div className="flex items-start justify-between">
								<div className="flex-1">
									<CardTitle className="text-lg mb-2">{process.title}</CardTitle>
									<CardDescription className="mb-3">{process.description}</CardDescription>
									<div className="flex flex-wrap gap-4 text-sm">
										<div className="flex items-center gap-2 text-muted-foreground">
											<FileText className="h-4 w-4" />
											<span>{process.processNumber}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<User className="h-4 w-4" />
											<span>{process.clientName}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<Calendar className="h-4 w-4" />
											<span>Atualizado: {new Date(process.lastUpdate).toLocaleDateString("pt-BR")}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<span>{process.updatesCount} andamentos</span>
										</div>
									</div>
								</div>
								<div className="flex flex-col items-end gap-2">
									<span className={`text-xs px-2 py-1 rounded-full ${
										process.status === "ConcluÃ­do" 
											? "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200"
											: process.status === "Em andamento"
											? "bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200"
											: "bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200"
									}`}>
										{process.status}
									</span>
									<Button size="sm" variant="outline">
										Gerenciar
									</Button>
								</div>
							</div>
						</CardHeader>
					</Card>
				))}
			</div>
		</div>
	);
};

export default ProcessosPage;

